import React, { useEffect, useState, useCallback, useRef } from 'react';
import { Device } from 'twilio-client';
import { setupVoiceDevice } from '../config/twilio';

const MAX_RECONNECT_ATTEMPTS = 5;
const INITIAL_RECONNECT_DELAY = 1000; // 1 second
const REGISTRATION_DELAY = 1000; // 1 second to ensure setup completion

const Voice = ({ onIncomingCall }) => {
    const [status, setStatus] = useState('Not Connected');
    const [error, setError] = useState(null);
    const [device, setDevice] = useState(null);
    const [config, setConfig] = useState(null);
    
    const reconnectAttempts = useRef(0);
    const isReconnecting = useRef(false);
    const reconnectTimeout = useRef(null);
    const currentDeviceRef = useRef(null);
    const deviceReadyRef = useRef(false);
    const registrationInProgress = useRef(false);

    const resetReconnectState = useCallback(() => {
        reconnectAttempts.current = 0;
        isReconnecting.current = false;
        deviceReadyRef.current = false;
        registrationInProgress.current = false;
        if (reconnectTimeout.current) {
            clearTimeout(reconnectTimeout.current);
            reconnectTimeout.current = null;
        }
    }, []);

    const isValidDevice = useCallback((device) => {
        return device && 
               typeof device.unregister === 'function' && 
               typeof device.destroy === 'function' &&
               typeof device.register === 'function';
    }, []);

    const destroyDevice = useCallback(async (currentDevice) => {
        if (!currentDevice) return;

        try {
            deviceReadyRef.current = false;
            registrationInProgress.current = false;
            
            if (currentDevice && typeof currentDevice.unregister === 'function') {
                try {
                    await currentDevice.unregister();
                } catch (unregisterError) {
                    console.warn('Error unregistering device:', unregisterError);
                }
            }
            
            if (currentDevice && typeof currentDevice.destroy === 'function') {
                try {
                    await currentDevice.destroy();
                } catch (destroyError) {
                    console.warn('Error destroying device:', destroyError);
                }
            }
            
            if (currentDeviceRef.current === currentDevice) {
                currentDeviceRef.current = null;
            }
        } catch (error) {
            console.error('Error during device cleanup:', error);
        }
    }, []);

    const registerDevice = useCallback(async (deviceToRegister) => {
        if (!deviceToRegister) {
            throw new Error('Device is required for registration');
        }

        if (!isValidDevice(deviceToRegister)) {
            throw new Error('Cannot register invalid device');
        }

        if (registrationInProgress.current) {
            console.log('Registration already in progress, skipping...');
            return false;
        }

        try {
            registrationInProgress.current = true;
            await deviceToRegister.register();
            return true;
        } catch (error) {
            console.error('Registration failed:', error);
            registrationInProgress.current = false;
            throw error;
        }
    }, [isValidDevice]);

    const handleDeviceError = useCallback(async (device, error) => {
        console.error('Device Error:', error);
        if (!isReconnecting.current) {
            if (error.code === 31102 || error.code === 31204 || error.code === 31009) {
                setStatus('Reconnecting');
                isReconnecting.current = true;
                try {
                    const delay = INITIAL_RECONNECT_DELAY * Math.pow(2, reconnectAttempts.current);
                    await new Promise(resolve => setTimeout(resolve, delay));

                    if (reconnectAttempts.current < MAX_RECONNECT_ATTEMPTS) {
                        reconnectAttempts.current++;
                        await initializeDevice();
                    } else {
                        setError('Failed to reconnect after multiple attempts. Please refresh the page.');
                        setStatus('Error');
                    }
                } finally {
                    isReconnecting.current = false;
                }
            } else {
                setError(error.message);
                setStatus('Error');
            }
        }
    }, []);

    const setupDevice = useCallback(async (token, options = {}) => {
        if (!token) {
            throw new Error('Token is required for device setup');
        }

        if (currentDeviceRef.current) {
            await destroyDevice(currentDeviceRef.current);
        }

        deviceReadyRef.current = false;
        registrationInProgress.current = false;
        const device = new Device();
        
        device.on('ready', () => {
            console.log('Device Ready');
            deviceReadyRef.current = true;
            setStatus('Connected');
            
            // Attempt registration when device is ready
            if (!registrationInProgress.current && isValidDevice(device)) {
                registrationInProgress.current = true;
                registerDevice(device).catch(error => {
                    console.error('Registration failed in ready handler:', error);
                    registrationInProgress.current = false;
                });
            }
        });

        device.on('error', (err) => handleDeviceError(device, err));

        device.on('registered', () => {
            console.log('Successfully registered with Twilio');
            setStatus('Registered');
            setError(null);
            resetReconnectState();
            registrationInProgress.current = false;
        });

        device.on('unregistered', () => {
            console.log('Unregistered from Twilio');
            setStatus('Unregistered');
            registrationInProgress.current = false;
        });

        device.on('offline', () => {
            console.log('Device is offline');
            setStatus('Offline');
            handleDeviceError(device, { code: 31009, message: 'Device went offline' });
        });
        
        const deviceOptions = {
            ...options,
            logLevel: 'debug',
            codecPreferences: ['opus', 'pcmu'],
            enableRegistration: true,
            closeProtection: true,
            edge: 'frankfurt',
            iceServers: [
                { urls: 'stun:stun.l.google.com:19302' }
            ]
        };

        try {
            await device.setup(token, deviceOptions);
            currentDeviceRef.current = device;
            return device;
        } catch (error) {
            console.error('Error during device setup:', error);
            await destroyDevice(device);
            throw error;
        }
    }, [destroyDevice, handleDeviceError, resetReconnectState, registerDevice]);

    const initializeDevice = useCallback(async () => {
        try {
            const newConfig = await setupVoiceDevice();
            setConfig(newConfig);

            if (!newConfig?.token || !newConfig?.sipDomain) {
                throw new Error('Invalid configuration received from server');
            }

            console.log('Got voice configuration:', {
                hasToken: true,
                tokenLength: newConfig.token.length,
                sipDomain: newConfig.sipDomain,
                extension: newConfig.extension
            });

            const newDevice = await setupDevice(newConfig.token);
            setDevice(newDevice);

            // Registration will be handled by the ready event
            console.log('Device setup complete, waiting for ready event...');

        } catch (error) {
            console.error('Error initializing device:', error);
            setError(error.message);
            setStatus('Error');
        }
    }, [setupDevice]);

    useEffect(() => {
        initializeDevice();

        return () => {
            resetReconnectState();
            if (currentDeviceRef.current) {
                destroyDevice(currentDeviceRef.current);
            }
        };
    }, [initializeDevice, resetReconnectState, destroyDevice]);

    return (
        <div className="voice-container">
            <div className="status-indicator">
                <div className={`status-dot ${status.toLowerCase()}`}></div>
                <span>Voice Status: {status}</span>
            </div>
            {error && (
                <div className="error-container">
                    <p className="error-message">Error: {error}</p>
                    <p className="error-help">
                        {reconnectAttempts.current > 0 
                            ? `Reconnection attempt ${reconnectAttempts.current}/${MAX_RECONNECT_ATTEMPTS}...` 
                            : 'Please check your network connection and refresh the page if the issue persists.'}
                    </p>
                </div>
            )}
            <div className="registration-info">
                {status === 'Connected' && <p>Device connected, waiting for registration...</p>}
                {status === 'Registered' && <p>Successfully registered with Twilio</p>}
                {status === 'Reconnecting' && <p>Attempting to reconnect...</p>}
                {status === 'Offline' && (
                    <p>
                        {isReconnecting.current 
                            ? `Attempting to reconnect (${reconnectAttempts.current}/${MAX_RECONNECT_ATTEMPTS})...` 
                            : 'Device is offline'}
                    </p>
                )}
                {status === 'Error' && reconnectAttempts.current >= MAX_RECONNECT_ATTEMPTS && (
                    <p>Maximum reconnection attempts reached. Please refresh the page.</p>
                )}
            </div>
        </div>
    );
};

export default Voice; 