import React from 'react';

const CallControls = ({
    call,
    onAnswer,
    onReject,
    onHangup
}) => {
    if (!call) {
        return null;
    }

    return (
        <div className="call-controls">
            <h3>Call Controls</h3>
            <div className="button-group">
                {call.status() === 'pending' && (
                    <>
                        <button onClick={onAnswer} className="answer-button">
                            Answer
                        </button>
                        <button onClick={onReject} className="reject-button">
                            Reject
                        </button>
                    </>
                )}
                {call.status() === 'open' && (
                    <button onClick={onHangup} className="hangup-button">
                        Hang Up
                    </button>
                )}
            </div>
        </div>
    );
};

export default CallControls; 