export const getVoiceAccess = async () => {
    // List of possible endpoints to try
    const endpoints = [
        'https://getcredentials-3757.twil.io/getCredentials',
        'https://getcredentials-3757.twil.io/access',  // Alternative URL from README
        'https://transfer-to-extension-1767.twil.io/getCredentials'  // Another possible domain
    ];

    let lastError = null;

    for (const endpoint of endpoints) {
        try {
            console.log('Attempting to fetch configuration from:', endpoint);
            const response = await fetch(endpoint, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            });

            if (!response.ok) {
                const errorText = await response.text();
                console.error('Server error from', endpoint, ':', response.status, errorText);
                lastError = new Error(`Server error: ${response.status} ${errorText}`);
                continue; // Try next endpoint
            }

            const data = await response.json();
            console.log('Received response from:', endpoint);
            
            // Validate the token and required fields
            if (!data.token) {
                console.error('Missing token in server response from:', endpoint);
                lastError = new Error('Authorization token not provided by server');
                continue; // Try next endpoint
            }

            if (!data.sipDomain || !data.extension) {
                console.error('Missing required fields in server response from:', endpoint);
                lastError = new Error('Missing required configuration from server');
                continue; // Try next endpoint
            }

            // Log successful retrieval (without exposing sensitive data)
            console.log('Successfully retrieved configuration from:', endpoint);
            
            // Return the configuration
            return {
                token: data.token,
                sipDomain: data.sipDomain || 'bigyox.sip.twilio.com',
                extension: data.extension,
                identity: data.identity || data.extension
            };
        } catch (error) {
            console.error('Error fetching from', endpoint, ':', error);
            lastError = error;
            // Continue to next endpoint
        }
    }

    // If we get here, all endpoints failed
    console.error('All endpoints failed to provide valid configuration');
    throw lastError || new Error('Failed to get voice configuration from any endpoint');
}; 