import React, { useState } from 'react';
import Voice from './components/Voice';
import CallControls from './components/CallControls';
import './App.css';

function App() {
  const [currentUser, setCurrentUser] = useState(null);
  const [callerId, setCallerId] = useState('');

  const handleIncomingCall = (user) => {
    setCurrentUser(user);
    // Get caller information from the user
    const remoteIdentity = user.session?.remoteIdentity;
    if (remoteIdentity) {
      setCallerId(remoteIdentity.uri.user);
    }
  };

  const handleAnswer = () => {
    if (currentUser) {
      currentUser.answer();
    }
  };

  const handleReject = () => {
    if (currentUser) {
      currentUser.hangup();
      setCurrentUser(null);
      setCallerId('');
    }
  };

  const handleHangup = () => {
    if (currentUser) {
      currentUser.hangup();
      setCurrentUser(null);
      setCallerId('');
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>SIP Phone</h1>
      </header>
      <main>
        <Voice onIncomingCall={handleIncomingCall} />
        {currentUser && (
          <div className="call-info">
            <h3>Incoming Call</h3>
            {callerId && <p>From: {callerId}</p>}
          </div>
        )}
        <CallControls
          call={currentUser}
          onAnswer={handleAnswer}
          onReject={handleReject}
          onHangup={handleHangup}
        />
      </main>
    </div>
  );
}

export default App; 