import { Device } from 'twilio-client';
import { getVoiceAccess } from '../services/twilioService';

let voiceDevice = null;

export const setupVoiceDevice = async () => {
    try {
        const config = await getVoiceAccess();
        
        // Validate token
        if (!config.token) {
            throw new Error('No token received from voice access');
        }

        // Log token length for debugging (don't log the actual token)
        console.log('Received token of length:', config.token.length);
        
        return {
            token: config.token,
            sipDomain: config.sipDomain,
            extension: config.extension,
            identity: config.identity
        };
    } catch (error) {
        console.error('Error setting up voice device:', error);
        throw error;
    }
};

export const getVoiceDevice = () => {
    if (!voiceDevice) {
        throw new Error('Voice device not initialized. Call setupVoiceDevice first.');
    }
    return voiceDevice;
}; 